import axios from 'axios'
import {FETCH_DATA_X, CHANGE_YEAR, SELECT_ENTITIES} from '../constants/action_types'


export function justdoit (type, fileName) {

	const change = {
		'test01.json': 1,
		'test02.json': 2,
		'test03.json': 3,
		'test04.json': 4,
		'test_color.json': 5,
	}

	const change2 = {
		'test01.json': 85,
		'test02.json': 84,
		'test03.json': 57,
		'test04.json': 60,
		'test_color.json': 5,
	}

	// const request = axios.get('/api/something/' + change[fileName])

	const request = axios.get('/api/amenity/' + change2[fileName] + '/1814,1815,1826,2666,2899')

	return {
		type: type,
		payload: request
	}
}

export function changeYear (newYear) {
	console.log('inside changeYear action')

	return {
		type: CHANGE_YEAR,
		value: newYear
	}
}

export function sendTheEntities (entities) {
	return {
		type: SELECT_ENTITIES,
		newSelectedEntities: entities
	}
}


